.page__body {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  font-size: 12px;
  list-style-type: none;
}

.container {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --column-count: 4;
  }
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  position: relative;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  align-items: center;
  display: flex;
}

.nav__list-item {
  margin-right: 20px;
}

.nav__list-item:last-child {
  margin-right: 0;
}

.nav__list-link {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  display: block;
}

.nav__list-link:hover {
  color: #00acdc;
  cursor: pointer;
  transition: all .3s;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.card__info {
  margin-bottom: 16px;
}

.card__info-img {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__info-text {
  color: #060b35;
  margin: 40px 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__info-code {
  color: #616070;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
}

.card__price-text:hover {
  color: #34568b;
}

.card__price-total {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__btn {
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  padding: 12px 70px 12px 69px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: block;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.stars.stars--4 {
  margin-bottom: 0;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  background-image: url("star.822d561e.svg");
  margin-right: 0;
}

.stars__rate-text {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

